import React, { Component } from 'react'
import { toast } from 'react-toastify'

class Prompt extends Component {

    constructor(props){
        super(props)

        this.state = {
            value: ""
        }
    }

    render() {
        return (
            <div>
                <div> We need you to authenticate please</div>
                <input value={this.state.value} onChange={(e) => {this.setState({value: e.target.value})}}/>
                <button onClick={() => {this.props.callback(this.state.value); this.props.closeToast()}}>Submit</button>
            </div>
        )
    }
}

const prompt = (callback, props) => {

    toast(<Prompt {...props} callback={callback}/>, {autoClose: false, closeOnClick: false, onClose: (arg) => {console.warn(arg)} })

}

export default prompt;
